<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </template>
    <template slot="main">
    <div style="background-color: #0dcaf0; background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0), rgb(248, 249, 250) 200px);">
        <div class="py-1 px-2">
            <div class="p-3">
      
            </div>
            <div class="d-flex justify-content-between align-items-center text-white py-2">
                <div class="bd-highlight">
                    <div class="d-flex flex-row bd-highlight align-items-center">
                        <div class="bd-highlight me-2" @click="$tourl('user_profile')">
                            <img class="rounded-pill border border-2 border-white" :src="$store.state.imgurl+'/assets/img/userimg/'+show.avatar+'.jpg'" width="50" height="50"/>
                        </div>
                        <div class="bd-highlight">
                            <div class="fw-bold fs-6"  @click="$tourl('user_nickname')">{{show.nickname}}</div>
                        </div>
                    </div>
                </div>
                <div class="bd-highlight"></div>
            </div>
            <div class="bg-white p-3 rounded shadow-sm fs-normal">
                <div class="d-flex justify-content-around">
                    <div class="bd-highlight text-center">
                        <div><span class="fs-4 fs-bold">{{show.createtime|timeint}}</span></div>
                        <div class="text-black-50"></div>
                    </div>
                    <div class="bd-highlight text-center" @click="$tourl('user_trip-passenger')" v-if="show.dint>0">
                        <div><span class="fs-4 fs-bold">{{show.dint}}</span></div>
                        <div class="text-black-50"></div>
                    </div>
                    <div class="bd-highlight text-center" @click="$tourl('user_trip-driver')">
                        <div><span class="fs-4 fs-bold">{{show.pint}}</span></div>
                        <div class="text-black-50"></div>
                    </div>
                </div>
            </div>
        </div>
        <Layout-frame :icons="'person-fill bg-primary'"  :title="''" v-if="data.plabel">
            <div class="px-1 fs-normal text-black-50"> <span class="text-primary">{{show.pevaluate.toFixed(1)}}</span>  {{data.plabel?'':''}}</div>
            <ViewList :layout="'py-2'" :type="'evaluate'" :set="{names:['','','','','','','','','','','','','']}" :data="data.plabel"/>
        </Layout-frame>
        <Layout-frame :icons="'heart-fill bg-warning'" :title="''">
            <div class="px-1 fs-normal text-black-50"> <span class="text-warning">{{show.devaluate.toFixed(1)}}</span>  {{data.dlabel?'':''}}</div>
            <ViewList :layout="'py-2'" :type="'evaluate'" :set="{names:['','','','','','','','','','','','','']}" :data="data.dlabel"/>
        </Layout-frame>
        <Layout-frame :icons="'github bg-warning'" :title="''" v-if="data.vehicle">
          <div class="p-2">
            <div class="p-1 fs-normal">
              <span class="fw-bold">{{data.vehicle.brand}}</span>
              <span class="ms-2 text-black-50">{{data.vehicle.typeinfo}} {{data.vehicle.colour}}</span>
            </div>
            <div>
              <img :src="data.vehicle.imgurl" width="100%" class="rounded border border-2 border-white">
            </div>
          </div>
        </Layout-frame>
    </div>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>


<script>
import ViewList from '../block/list';
import LayoutFrame from '../block/layout/frame-1';
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
    LayoutFrame,
    ViewList,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      show:{},
    }
  },
  created(){
    this.show=this.data.user;
  },
}
</script>

<style>

</style>


